@import "second";

@import "fonts";


body{
	margin: 0;
	padding: 0;
	font-family: 'Inter', sans-serif;
}
a{
	text-decoration: none;
}

// modal
.modals{
	a {
			cursor: pointer;
			position: absolute;
			right: 25px;
			top: 25px;
			transform: rotate(-45deg);
		}
}

.modal-overlay {
	z-index: 9999999999999;
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.7);
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100vh;
	width: 100%;
	opacity: 0;
	visibility: hidden;
	transition: all 0.3s ease-in-out;
}
.modalsHide{
	display: none;
}
.modal {
	z-index: 9999999999999;
	background-color: #fff;
	display: flex;
	align-items: center;
	justify-content: center;
	display: none;
	padding: 60px;
	max-width: 380px;
	border-radius: 30px;
	.form__content{
		text-align: center;
		h2{
			margin-top: 0;
			margin-bottom: 20px;
			font-size: 42px;
			font-weight: 700;
		}
		div{
			width: 100%;
		}
		input{
			padding: 0;
			padding-left: 20px;
			padding-right: -20px;
			border: 1px solid #c9c9c9;
			border-radius: 15px;
			font-size: 14px;
			width: calc(100% - 20px);
			height: 50px;
			margin-bottom: 20px;
			outline: none;
		}
		button{
			cursor: pointer !important;
			outline: none;
			border: 1px solid #565ea7;
			width: 100%;
			padding: 20px;
		}
	}
}

.modal-overlay--visible {
	opacity: 1;
	visibility: visible;
	transition: all 0.3s ease-in-out;
}

.modal--visible {
	display: block;
}

.topNubex {
	opacity: 1;
	transition: .2s;
	z-index: 9999999999;
	position: fixed;
	left: 45px;
	bottom: 45px;
	width: 35px;
	height: 35px;
	background-color: rgb(255, 255, 255);
	border-radius: 50%;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
	padding: 5px;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: .3s;
	svg{
		width: 30px;
		height: 30px;
	}
	&:hover{
		svg{
			fill: #fff;
		}
		background-color: rgb(86, 94, 167);
	}
}
.topHide{
	opacity: 0 !important;
}
.sidebar__btn {
	position: fixed;
	right: 15px;
	top: -10px;
	width: 45px;
	height: 45px;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 7px;
	background-color: rgb(86, 94, 167);
	margin-top: 25px;
