	width: 100%;
	display: flex;
	justify-content: start;
	align-items: center;
		&::after{
		content: '';
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		background-color: rgba(0, 0, 0, 0.6);
	}
				.content{
