.grid__item{
	overflow: hidden !important;
	position: relative;
	display: flex;
	align-items: flex-end;
	border-radius: 15px;
	img{
		z-index: 1;
		transition: .5s;
		object-fit: cover;
		object-position: center;
		border-radius: 15px;
		width: 100%;
	}
	.black{
		z-index: 2;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.5);
	}
	div{
		z-index: 3;
		padding: 30px;
		position: absolute;
		z-index: 1000;
		h1{
			color: #b6b8fc;
			font-size: 24px;
			font-weight: 700;
		}
		p{
			font-size: 16px;
			margin-top: 15px;
			font-weight: 400;
			color: #fff;
		}
	}
	&:hover{
		img{
			transform: scale(1.05);
		}
	}
}

.grid{
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-auto-rows: 280px;
	gap: 1.5rem;
}
.grid__item_1{
	grid-column-start: 1;
	grid-column-end: 3;
	grid-row-start: 1;
	grid-row-end: 3;
}

.grid__item_2 {
	grid-column-start: 3;
	grid-column-end: 4;
	grid-row-start: 1;
	grid-row-end: 2;
}

.grid__item_3 {
	grid-column-start: 4;
	grid-column-end: 5;
	grid-row-start: 1;
	grid-row-end: 2;
}

.grid__item_4 {
	grid-column-start: 3;
	grid-column-end: 5;
	grid-row-start: 2;
	grid-row-end: 3;
}

